import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'; //   useDispatch  useSelector
import { setTheme } from '../../redux/ThemeSlice'; //     
import { RootState } from '../../redux/store';
import GlobalSvgSelector from '../../assets/icons/global/GlobalSvgSelector';
import CreatableSelect from 'react-select/creatable';
import { AppDispatch } from '../../redux/store';
import { fetchWeather } from '../../redux/weatherThunks';
import { setCity } from '../../redux/WeatherSlice';
import styles from './Header.module.scss'

function Header() {
  const dispatch = useDispatch<AppDispatch>();
  const theme = useSelector((state: RootState) => state.theme.theme);

  useEffect(() => {
    const savedTheme = localStorage.getItem('theme') as 'light' | 'dark' | null;
    if (savedTheme === 'light' || savedTheme === 'dark') {
      dispatch(setTheme(savedTheme));
    }
  }, [dispatch]);

  //      localStorage
  const handleChangeTheme = () => {
    const newTheme = theme === 'light' ? 'dark' : 'light';
    dispatch(setTheme(newTheme));

    localStorage.setItem('theme', newTheme);
  };

  // interface Option {
  //   value: string;
  //   label: string;
  // }
  const cities = [
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '-', label: '-' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: ' ', label: ' ' },
    { value: '', label: '' },
    { value: ' ', label: ' ' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
  ];
  const colorStyles = {
    control: (baseStyles: any) => ({
      ...baseStyles,
      backgroundColor: theme === 'light' ? '#c3e0ff' : '#fff',
      width: '220px',
      height: '38px',
      border: 'none',
      borderRadius: '10px',
      zIndex: 100,
    }),
    singleValue: (baseStyles: any) => ({
      ...baseStyles,
      color: '#000',
    }),
    placeholder: (baseStyles: any) => ({
      ...baseStyles,
      color: '#000',
      fontStyle: 'italic',
      fontSize: '14px',
    }),
    dropdownIndicator: (baseStyles: any) => ({
      ...baseStyles,
      color: '#000',
    }),
    indicatorSeparator: (baseStyles: any) => ({
      ...baseStyles,
      backgroundColor: '#000',
    }),
  };

  useEffect(() => {
    const root = document.querySelector(':root') as HTMLElement;

    const components = [
      'container-background',
      'components-background',
      'card-background',
      'card-shadow',
      'text-color',
    ];
    components.forEach((comp) => {
      root.style.setProperty(`--${comp}-default`, `var(--${comp}-${theme})`);
    });
  }, [theme]);

  const handleCityChange = (selectedOption: { value: string; label: string } | null) => {
    if (selectedOption) {
      const city = selectedOption.value;
      dispatch(setCity(city));
      dispatch(fetchWeather(city));
    }
  };

  return (
    <header className={styles.header}>
      <div className={styles.wrapper}>
        <div className={styles.logo}>
          <GlobalSvgSelector id="header-logo" />
        </div>
        <div className={styles.title}>Weather</div>
      </div>
      <div className={styles.wrapper}>
        <div className={styles.change_theme} onClick={handleChangeTheme}>
          <GlobalSvgSelector id="change_theme" />
        </div>
        <CreatableSelect
          options={cities}
          styles={colorStyles}
          onChange={handleCityChange}
          placeholder=" ..."
          isClearable
        />
      </div>
    </header>
  );
}

export default Header;
